import { Direction } from './utils';

export type DirectionObserver = (direction: Direction) => void;

export default class DirectionSubject {
  private observers: DirectionObserver[] = [];

  public attach(observer: DirectionObserver) {
    this.observers.push(observer);
  }

  public detach(observer: DirectionObserver) {
    this.observers = this.observers.filter(
      (currentObserver) => currentObserver !== observer
    );
  }

  public notify(direction: Direction) {
    this.observers.forEach((observer) => observer(direction));
  }

  public destroy() {
    this.observers = [];
  }
}
