import React, { MutableRefObject, useEffect, useRef } from 'react';

import Cell from '../../engine/Cell';
import GameEngine from '../../engine/Game';
import { GameSnapshot } from '../../engine/Utils/utils';
import './snake-game.scss';
import Renderer from './util/Renderer';

const cellSize = 35;

interface SnakeGameProps {
  gameEngine: GameEngine | null;
}

function SnakeGame({ gameEngine }: SnakeGameProps) {
  const boardRef = useRef<HTMLDivElement>() as MutableRefObject<HTMLDivElement>;

  useEffect(() => {
    if (gameEngine) {
      gameEngine.onSnapshotSubject.attach(renderGame);

      const { widthCells, heightCells } = gameEngine.getSize();

      Renderer.generateBoard(
        widthCells * cellSize,
        heightCells * cellSize,
        'snake-game__board'
      );

      gameEngine.startGame();
    }

    window.addEventListener('resize', handleViewportResize);

    return () => {
      window.removeEventListener('resize', handleViewportResize);
    };
  }, [gameEngine]);

  const renderGame = (gameSnapshot: GameSnapshot) => {
    const cherries: Cell[] = [];
    const mushrooms: Cell[] = [];
    const pizzas: Cell[] = [];
    const snake: Cell[] = [];
    const walls: Cell[] = [];

    gameSnapshot.cells.forEach((cell) => {
      switch (cell.type) {
        case 'cherry':
          cherries.push(cell);
          break;
        case 'mushroom':
          mushrooms.push(cell);
          break;
        case 'pizza':
          pizzas.push(cell);
          break;
        case 'snake':
          snake.push(cell);
          break;
        case 'wall':
          walls.push(cell);
      }
    });

    Renderer.renderBlocks('wall', walls, cellSize);
    Renderer.renderImages('cherry', cherries, cellSize);
    Renderer.renderImages('mushroom', mushrooms, cellSize);
    Renderer.renderImages('pizza', pizzas, cellSize);
    Renderer.renderBlocks('snake', snake, cellSize);
  };

  const handleViewportResize = () => {
    const boardBoundingRect = boardRef.current.getBoundingClientRect();

    if (gameEngine) {
      Renderer.remove();

      const widthCells = Math.floor(boardBoundingRect.width / cellSize);
      const heightCells = Math.floor(boardBoundingRect.height / cellSize);

      gameEngine.endGame();
      gameEngine.setSize(widthCells, heightCells);

      Renderer.generateBoard(
        widthCells * cellSize,
        heightCells * cellSize,
        'snake-game__board'
      );
    }
  };

  return (
    <div className="snake-game">
      <div className="snake-game__board" ref={boardRef} />
    </div>
  );
}

export default SnakeGame;
