import {
  IKeyboardService,
  KeyboardServiceConfig,
} from '../../engine/KeyboardService';
import DirectionSubject from '../../engine/Utils/DirectionSubject';
import TriggerSubject from '../../engine/Utils/TriggerSubject';
import { Direction, GameWindow, randomBetween } from '../../engine/Utils/utils';

const directions = ['up', 'down', 'left', 'right'];

export default class DemoKeyboardService implements IKeyboardService {
  private window: GameWindow;

  isInverse = false;

  interval: number | null = null;

  public onSpaceSubject = new TriggerSubject();

  public onDirectionSubject = new DirectionSubject();

  constructor({ window }: KeyboardServiceConfig) {
    this.window = window;
    this.triggerRandomDirectionKey = this.triggerRandomDirectionKey.bind(this);
  }

  public init() {
    this.setInterval();
  }

  public destroy() {
    this.clearInterval();
  }

  setInterval() {
    this.interval = this.window.setInterval(
      this.triggerRandomDirectionKey,
      1000
    );
  }

  clearInterval() {
    if (this.interval) {
      this.window.clearInterval(this.interval);
    }
  }

  triggerRandomDirectionKey() {
    this.onDirectionSubject.notify(
      directions[randomBetween(0, directions.length - 1)] as Direction
    );
  }

  public inverse() {
    this.isInverse = !this.isInverse;
  }
}
