import { IBoard } from './Board';
import { IKeyboardService } from './KeyboardService';
import { IScoreService } from './ScoreService';
import GameSnapshotSubject from './Utils/GameSnapshotSubject';
import { ITimerService } from './Utils/TimerService';
import TriggerSubject from './Utils/TriggerSubject';
import { Direction, GameWindow } from './Utils/utils';

const Scores = {
  cherry: 100,
  mushroom: 350,
  pizza: 400,
};

interface GameConfig {
  initialSpeed?: number;
  window: GameWindow;
  keyboardService: IKeyboardService;
  scoreService: IScoreService;
  board: IBoard;
  timerService: ITimerService;
}

type GameState = 'not-started' | 'started' | 'paused' | 'over';

export default class Game {
  private window: GameWindow;

  private speed: number;

  private state: GameState = 'not-started';

  private board: IBoard;

  private keyboardService: IKeyboardService;

  private timerService: ITimerService;

  private scoreService: IScoreService;

  private currentDirection: Direction = 'right';

  private timeout: number | null = null;

  private reverseDirectionTimer: number | null = null;

  public onPauseSubject = new TriggerSubject();

  public onGameOverSubject = new TriggerSubject();

  public onSnapshotSubject = new GameSnapshotSubject();

  constructor({
    initialSpeed = 300,
    window,
    keyboardService,
    scoreService,
    board,
    timerService,
  }: GameConfig) {
    this.window = window;
    this.keyboardService = keyboardService;
    this.scoreService = scoreService;
    this.board = board;
    this.timerService = timerService;

    this.speed = initialSpeed;

    this.revertControls = this.revertControls.bind(this);
    this.tick = this.tick.bind(this);
    this.onDirectionChange = this.onDirectionChange.bind(this);
    this.onSpaceHandler = this.onSpaceHandler.bind(this);

    this.keyboardService.onSpaceSubject.attach(this.onSpaceHandler);
    this.keyboardService.onDirectionSubject.attach(this.onDirectionChange);

    this.keyboardService.init();
  }

  public startGame() {
    this.state = 'started';
    this.setTimeout();
  }

  public endGame() {
    if (this.state === 'over') return;
    if (this.scoreService.get()) this.scoreService.save();
    this.state = 'over';
    this.clearTimeout();
    this.onGameOverSubject.notify();
  }

  private pauseGame() {
    this.state = 'paused';
    if (this.reverseDirectionTimer) {
      this.timerService.pause();
    }
    this.clearTimeout();
  }

  private resumeGame() {
    this.state = 'started';
    this.setTimeout();
    if (this.reverseDirectionTimer) {
      this.timerService.resume();
    }
  }

  public restartGame() {
    this.scoreService.reset();
    this.speed = 300;
    this.board.reset();
    this.currentDirection = 'right';
    if (this.reverseDirectionTimer) {
      this.timerService.cancel();
      this.revertControls();
    }
    this.state = 'started';
    this.setTimeout();
  }

  private onDirectionChange(direction: Direction) {
    if (this.state === 'paused') return;

    if (this.currentDirection === direction) {
      return;
    }

    if (
      (direction === 'up' && this.currentDirection === 'down') ||
      (direction === 'down' && this.currentDirection === 'up') ||
      (direction === 'left' && this.currentDirection === 'right') ||
      (direction === 'right' && this.currentDirection === 'left')
    ) {
      return;
    }

    this.currentDirection = direction;
  }

  private onSpaceHandler() {
    if (this.state === 'paused') {
      this.state = 'started';
      this.resumeGame();
    } else {
      this.state = 'paused';
      this.pauseGame();
    }
    this.onPauseSubject.notify();
  }

  private moveSnake() {
    this.board.moveSnake(this.currentDirection);
  }

  public getCells() {
    return this.board.getCells();
  }

  private tick() {
    const nextCell = this.board.getNextSnakeCellByDirection(
      this.currentDirection
    );

    this.timeout = this.window.setTimeout(this.tick, this.speed);

    switch (nextCell.type) {
      case 'empty':
        this.moveSnake();
        break;
      case 'cherry':
        this.moveSnake();
        this.scoreService.add(Scores.cherry);
        break;
      case 'mushroom':
        this.moveSnake();
        this.scoreService.add(Scores.mushroom);
        this.inverseControls();
        break;
      case 'pizza':
        this.moveSnake();
        this.scoreService.add(Scores.pizza);
        if (this.speed - 30 > 0) {
          this.speed -= 30;
        }
        break;
      case 'wall':
      case 'snake':
        this.endGame();
        break;
      default:
        break;
    }

    this.onSnapshotSubject.notify({
      cells: this.board.getCells(),
      score: this.scoreService.get(),
    });
  }

  private setTimeout() {
    this.timeout = this.window.setTimeout(this.tick, this.speed);
  }

  private clearTimeout() {
    if (this.timeout) {
      this.window.clearTimeout(this.timeout);
      this.timeout = null;
    }
  }

  private inverseControls() {
    if (!this.reverseDirectionTimer) {
      this.reverseDirectionTimer = this.timerService.start(
        this.revertControls,
        30000
      );
      this.keyboardService.inverse();
    }
  }

  private revertControls() {
    this.keyboardService.inverse();
    this.reverseDirectionTimer = null;
  }

  public setSize(widthCells: number, heightCells: number) {
    this.board.setSize(widthCells, heightCells);
  }

  public getSize(): { widthCells: number; heightCells: number } {
    return this.board.getSize();
  }

  public getState() {
    return this.state;
  }

  public getSpeed() {
    return this.speed;
  }

  public destroy() {
    this.clearTimeout();

    this.onPauseSubject.destroy();
    this.onGameOverSubject.destroy();
    this.onSnapshotSubject.destroy();

    this.board.destroy();

    this.keyboardService.destroy();

    if (this.timerService) this.timerService.destroy();
  }
}
