import DirectionSubject from './Utils/DirectionSubject';
import TriggerSubject from './Utils/TriggerSubject';
import { GameWindow } from './Utils/utils';

export interface IKeyboardService {
  onSpaceSubject: TriggerSubject;
  onDirectionSubject: DirectionSubject;
  init: () => void;
  destroy: () => void;
  inverse: () => void;
}

export interface KeyboardServiceConfig {
  window: GameWindow;
}

export default class KeyboardService implements IKeyboardService {
  private window: GameWindow;

  private isInverse = false;

  public onSpaceSubject = new TriggerSubject();

  public onDirectionSubject = new DirectionSubject();

  constructor({ window }: KeyboardServiceConfig) {
    this.window = window;
    this.keyDownHandler = this.keyDownHandler.bind(this);
  }

  private keyDownHandler(event: KeyboardEvent) {
    switch (event.key) {
      case 'w':
      case 'ArrowUp':
        this.onDirectionSubject.notify(this.isInverse ? 'down' : 'up');
        break;
      case 's':
      case 'ArrowDown':
        this.onDirectionSubject.notify(this.isInverse ? 'up' : 'down');
        break;
      case 'a':
      case 'ArrowLeft':
        this.onDirectionSubject.notify(this.isInverse ? 'right' : 'left');
        break;
      case 'd':
      case 'ArrowRight':
        this.onDirectionSubject.notify(this.isInverse ? 'left' : 'right');
        break;
      case ' ':
        this.onSpaceSubject.notify();
        break;
      default:
        break;
    }
  }

  public init() {
    this.window.addEventListener('keydown', this.keyDownHandler);
  }

  public destroy() {
    this.window.removeEventListener('keydown', this.keyDownHandler);
    this.onSpaceSubject.destroy();
    this.onDirectionSubject.destroy();
  }

  public inverse() {
    this.isInverse = !this.isInverse;
  }
}
