import React from 'react';

import Card from '../../components/card/card';
import DemoSnakeGame from '../../components/demo-snake-game/demo-snake-game';
import MenuLink from '../../components/menu-link/menu-link';
import ScoreService from '../../engine/ScoreService';
import './high-score.scss';

function HighScore() {
  const scores = ScoreService.getScores();
  const content = scores.length ? (
    <>
      <h2>Highscore</h2>
      {scores.reverse().map((score) => (
        <p key={Math.random()}>{score}</p>
      ))}
    </>
  ) : (
    <h2>No scores yet!</h2>
  );

  return (
    <div className="high-score">
      <DemoSnakeGame />
      <Card>
        {content}
        <MenuLink to="/game">Start new game</MenuLink>
      </Card>
    </div>
  );
}

export default HighScore;
