import clsx from 'clsx';
import React from 'react';

import Card from '../../../components/card/card';
import './score.scss';

interface ScoreProps {
  score?: number;
  className?: string;
}

function Score({ score, className }: ScoreProps) {
  return (
    <Card className={clsx('score', className)}>
      <p>
        <span>SCORE: </span>
        <span>{score}</span>
      </p>
    </Card>
  );
}

Score.defaultProps = {
  score: 0,
  className: '',
};

export default Score;
