import clsx from 'clsx';
import React from 'react';
import { Link } from 'react-router-dom';

import './menu-link.scss';

type MenuLinkType = 'primary' | 'secondary' | 'default';

interface MenuLinkProps {
  to: string;
  type?: MenuLinkType;
  children: React.ReactNode;
}

function MenuLink({ to, type, children }: MenuLinkProps) {
  return (
    <Link className={clsx(`menu-link`, `menu-link--${type}`)} to={to}>
      {children}
    </Link>
  );
}

MenuLink.defaultProps = {
  type: 'default',
};

export default MenuLink;
