import clsx from 'clsx';
import React from 'react';

import './card.scss';

interface CardProps {
  children: React.ReactNode;
  className?: string;
}

function Card({ children, className }: CardProps) {
  return <div className={clsx('card', className)}>{children}</div>;
}

Card.defaultProps = {
  className: '',
};

export default Card;
