import Cell, { CellType } from './Cell';
import { ICellPositionService } from './CellPositionService';
import { Direction, randomBetween } from './Utils/utils';

export interface IBoard {
  moveSnake: (direction: Direction) => void;
  getCells: () => Cell[];
  getNextSnakeCellByDirection: (direction: Direction) => Cell;
  reset: () => void;
  setSize: (widthCells: number, heightCells: number) => void;
  getSize: () => { widthCells: number; heightCells: number };
  destroy: () => void;
}

interface BoardConfig {
  widthCells: number;
  heightCells: number;
  autoGenerateFood?: boolean;
  cellPositionService: ICellPositionService;
}

const DirectionVector = {
  up: {
    x: 0,
    y: -1,
  },
  down: {
    x: 0,
    y: 1,
  },
  left: {
    x: -1,
    y: 0,
  },
  right: {
    x: 1,
    y: 0,
  },
};

export default class Board implements IBoard {
  private widthCells: number = 0;

  private heightCells: number = 0;

  private snakeCells: Cell[] = [];

  private autoGenerateFood = true;

  private maxFood = 0;

  private cellPositionService: ICellPositionService;

  constructor({
    widthCells = 10,
    heightCells = 10,
    autoGenerateFood = true,
    cellPositionService,
  }: BoardConfig) {
    this.cellPositionService = cellPositionService;
    this.autoGenerateFood = autoGenerateFood;

    this.init(widthCells, heightCells);
  }

  private init(widthCells: number, heightCells: number) {
    this.cellPositionService.clear();

    this.widthCells = widthCells;
    this.heightCells = heightCells;

    if (this.autoGenerateFood) {
      this.maxFood = this.generateMaxFoodFromCellNumber();
    }

    this.initSnake();

    this.initWalls();

    this.initFood(this.maxFood, 'cherry');
    this.initFood(this.maxFood, 'mushroom');
    this.initFood(this.maxFood, 'pizza');
  }

  private initWalls() {
    for (let i = 0; i < this.heightCells; i++) {
      if (i === 0 || i === this.heightCells - 1) {
        for (let j = 0; j < this.widthCells; j++) {
          const cell = new Cell({ x: j, y: i, type: 'wall' });

          this.cellPositionService.set(cell);
        }
      } else {
        const leftWallcell = new Cell({ x: 0, y: i, type: 'wall' });
        const rightWallcell = new Cell({
          x: this.widthCells - 1,
          y: i,
          type: 'wall',
        });

        this.cellPositionService.set(leftWallcell);
        this.cellPositionService.set(rightWallcell);
      }
    }
  }

  private initFood(totalFood: number, type: CellType) {
    for (let i = 0; i < totalFood; i++) {
      const { x, y } = this.getRandomFreeCoordinates();

      const cell = new Cell({ x, y, type });

      this.cellPositionService.set(cell);
    }
  }

  private initSnake() {
    const x = Math.floor((this.widthCells - 2) / 2);
    const y = Math.floor((this.heightCells - 2) / 2);

    const head = new Cell({ x, y, type: 'snake' });
    this.cellPositionService.set(head);

    const tail = new Cell({ x: x - 1, y, type: 'snake' });
    this.cellPositionService.set(tail);

    this.snakeCells.push(head, tail);
  }

  private createNewFood(type: CellType) {
    const { x, y } = this.getRandomFreeCoordinates();
    const cell = new Cell({ x, y, type });

    this.cellPositionService.set(cell);
  }

  public moveSnake(direction: Direction) {
    const { x, y } = this.getNextCellCoordinates(direction);

    const existingCell = this.cellPositionService.get(x, y);

    const newSnakeCell = new Cell({ x, y, type: 'snake' });

    this.snakeCells.unshift(newSnakeCell);
    this.cellPositionService.set(newSnakeCell);

    if (!existingCell) {
      const snakeTail = this.snakeCells.pop() as Cell;
      this.cellPositionService.remove(snakeTail);
    } else {
      this.createNewFood(existingCell.type);
    }
  }

  public getCells() {
    return this.cellPositionService.getAll();
  }

  public getNextSnakeCellByDirection(direction: Direction): Cell {
    const { x, y } = this.getNextCellCoordinates(direction);

    const cell = this.cellPositionService.get(x, y);

    if (cell) {
      return cell;
    }

    return new Cell({
      x,
      y,
      type: 'empty',
    });
  }

  private getNextCellCoordinates(
    direction: Direction
  ): { x: number; y: number } {
    const directionVectorX = DirectionVector[direction].x;
    const directionVectorY = DirectionVector[direction].y;

    const snakeHead = this.snakeCells[0];

    const snakeX = snakeHead.x;
    const snakeY = snakeHead.y;

    const nextX = snakeX + directionVectorX;
    const nextY = snakeY + directionVectorY;

    return {
      x: nextX,
      y: nextY,
    };
  }

  private getRandomFreeCoordinates() {
    let x = randomBetween(1, this.widthCells - 1);
    let y = randomBetween(1, this.heightCells - 1);

    while (this.cellPositionService.has(x, y)) {
      x = randomBetween(1, this.widthCells - 1);
      y = randomBetween(1, this.heightCells - 1);
    }

    return { x, y };
  }

  public reset() {
    this.cellPositionService.clear();
    this.snakeCells = [];

    this.initSnake();

    this.initWalls();

    this.initFood(this.maxFood, 'cherry');
    this.initFood(this.maxFood, 'mushroom');
    this.initFood(this.maxFood, 'pizza');
  }

  private generateMaxFoodFromCellNumber() {
    const cells = this.widthCells * this.heightCells;
    if (cells < 700) return 1;
    if (cells < 1400) return 2;
    if (cells < 2100) return 3;
    return 4;
  }

  public setSize(widthCells: number, heightCells: number) {
    this.init(widthCells, heightCells);
  }

  public getSize(): { widthCells: number; heightCells: number } {
    return {
      widthCells: this.widthCells,
      heightCells: this.heightCells,
    };
  }

  public destroy() {
    this.cellPositionService.clear();
    this.snakeCells = [];
  }
}
