import React, { MutableRefObject, useEffect, useRef, useState } from 'react';

import MobileControls from '../../components/mobile-controls/mobile-controls';
import SnakeGame from '../../components/snake-game/snake-game';
import Board from '../../engine/Board';
import CellPositionService from '../../engine/CellPositionService';
import GameEngine from '../../engine/Game';
import KeyboardService from '../../engine/KeyboardService';
import ScoreService from '../../engine/ScoreService';
import TimerService from '../../engine/Utils/TimerService';
import { GameSnapshot } from '../../engine/Utils/utils';
import GameOver from './game-over/game-over';
import './game.scss';
import Paused from './paused/paused';
import Score from './score/score';

const cellSize = 35;

function Game() {
  const [score, setScore] = useState(0);
  const [gameOver, setGameOver] = useState(false);
  const [paused, setPaused] = useState(false);
  const [game, setGame] = useState<GameEngine | null>(null);

  const boardRef = useRef<HTMLDivElement>() as MutableRefObject<HTMLDivElement>;

  useEffect(() => {
    const engine = initGame();
    setGame(engine);
    return () => {
      engine.destroy();
    };
  }, []);

  const initGame = (): GameEngine => {
    const boardBoundingRect = boardRef.current.getBoundingClientRect();

    const widthCells = Math.floor(boardBoundingRect.width / cellSize);
    const heightCells = Math.floor(boardBoundingRect.height / cellSize);

    const engine = new GameEngine({
      window,
      keyboardService: new KeyboardService({ window }),
      scoreService: new ScoreService(),
      board: new Board({
        widthCells,
        heightCells,
        cellPositionService: new CellPositionService(),
      }),
      timerService: new TimerService({ window }),
    });

    engine.onSnapshotSubject.attach((gameSnapshot: GameSnapshot) => {
      setScore(gameSnapshot.score);
    });

    engine.onGameOverSubject.attach(() => {
      setPaused(false);
      setGameOver(true);
    });

    engine.onPauseSubject.attach(() => {
      if (engine.getState() === 'over') return;
      setPaused((pausedState) => !pausedState);
    });
    return engine;
  };

  const restart = () => {
    if (game) {
      setGameOver(false);
      game.restartGame();
    }
  };

  return (
    <div className="game">
      <Score score={score} />
      <div className="game__board" ref={boardRef}>
        <SnakeGame gameEngine={game} />
      </div>
      {gameOver ? <GameOver onRestart={restart} /> : null}
      {paused && !gameOver ? <Paused /> : null}
      <MobileControls />
    </div>
  );
}

export default Game;
