export type CellType =
  | 'snake'
  | 'cherry'
  | 'mushroom'
  | 'pizza'
  | 'empty'
  | 'wall';

interface CellConfig {
  x: number;
  y: number;
  type: CellType;
}

export default class Cell {
  public x: number;

  public y: number;

  public type: CellType;

  constructor({ x, y, type }: CellConfig) {
    this.x = x;
    this.y = y;
    this.type = type;
  }
}
