export type TriggerObserver = () => void;

export default class TriggerSubject {
  private observers: TriggerObserver[] = [];

  public attach(observer: TriggerObserver) {
    this.observers.push(observer);
  }

  public detach(observer: TriggerObserver) {
    this.observers = this.observers.filter(
      (currentObserver) => currentObserver !== observer
    );
  }

  public notify() {
    this.observers.forEach((observer) => observer());
  }

  public destroy() {
    this.observers = [];
  }
}
