export interface IScoreService {
  add: (score: number) => void;
  reset: () => void;
  get: () => number;
  save: () => void;
}

export default class ScoreService implements IScoreService {
  static getScores() {
    const savedScores = localStorage.getItem('scores');
    const scores: [] = savedScores ? JSON.parse(savedScores).map(Number) : [];
    return scores;
  }

  private score = 0;

  public add(score: number) {
    this.score += score;
  }

  public reset() {
    this.score = 0;
  }

  public get(): number {
    return this.score;
  }

  public save() {
    const scores = ScoreService.getScores();
    const newScores = [...scores, this.score].sort((a, b) => a - b).splice(-5);
    const uniqueScores = new Set(newScores);
    localStorage.setItem('scores', JSON.stringify(Array.from(uniqueScores)));
  }
}
