import * as d3 from 'd3';

import Cell, { CellType } from '../../../engine/Cell';

export default class Renderer {
  static generateBoard(width: number, height: number, boardCssClass: string) {
    d3.select(`.${boardCssClass}`)
      .append('svg')
      .attr('class', 'snake-game__svg')
      .attr('width', width)
      .attr('height', height);
  }

  static renderBlocks(type: CellType, cells: Cell[], cellSize: number) {
    const svg = d3.select('.snake-game__svg');

    const blocks = svg
      .selectAll(`rect.${type}`)
      .data(cells, (d) => JSON.stringify(d));

    blocks
      .enter()
      .append('rect')
      .attr('class', type)
      .attr('width', cellSize)
      .attr('height', cellSize)
      .attr('x', (point) => point.x * cellSize)
      .attr('y', (point) => point.y * cellSize);

    blocks.exit().remove();
  }

  static renderImages(type: CellType, cells: Cell[], cellSize: number) {
    const svg = d3.select('.snake-game__svg');

    const images = svg
      .selectAll(`image.${type}`)
      .data(cells, (d) => JSON.stringify(d));

    images
      .enter()
      .append('svg:image')
      .attr('class', type)
      .attr('width', cellSize)
      .attr('height', cellSize)
      .attr('x', (point) => point.x * cellSize)
      .attr('y', (point) => point.y * cellSize)
      .attr('xlink:href', `/assets/${type}.svg`);

    images.exit().remove();
  }

  static remove() {
    d3.select('.snake-game__svg').remove();
  }
}
