import React from 'react';

import Card from '../../../components/card/card';
import './paused.scss';

function Paused() {
  return (
    <div className="paused">
      <div className="paused__backdrop" />
      <Card className="paused__card">
        <h2 className="paused__title">Paused</h2>
      </Card>
    </div>
  );
}

export default Paused;
