import Cell from './Cell';

export interface ICellPositionService {
  set: (cell: Cell) => void;
  remove: (cell: Cell) => void;
  has: (x: number, y: number) => boolean;
  get: (x: number, y: number) => Cell;
  getAll: () => Cell[];
  clear: () => void;
}

export default class CellPositionService implements ICellPositionService {
  private positions = new Map();

  public set(cell: Cell) {
    this.positions.set(`${cell.x},${cell.y}`, cell);
  }

  public remove(cell: Cell) {
    this.positions.delete(`${cell.x},${cell.y}`);
  }

  public has(x: number, y: number): boolean {
    return this.positions.has(`${x},${y}`);
  }

  public get(x: number, y: number): Cell {
    return this.positions.get(`${x},${y}`);
  }

  public getAll() {
    return Array.from(this.positions.values());
  }

  public clear() {
    this.positions.clear();
  }
}
