import { GameSnapshot } from './utils';

export type GameSnapshotObserver = (snapshot: GameSnapshot) => void;

export default class GameSnapshotSubject {
  private observers: GameSnapshotObserver[] = [];

  public attach(observer: GameSnapshotObserver) {
    this.observers.push(observer);
  }

  public detach(observer: GameSnapshotObserver) {
    this.observers = this.observers.filter(
      (currentObserver) => currentObserver !== observer
    );
  }

  public notify(snapshot: GameSnapshot) {
    this.observers.forEach((observer) => observer(snapshot));
  }

  public destroy() {
    this.observers = [];
  }
}
