import React from 'react';

import MenuButton from '../menu-button/menu-button';
import './mobile-controls.scss';

function MobileControls() {
  const trigger = (key: string) => {
    const ev = new KeyboardEvent('keydown', { key });
    window.dispatchEvent(ev);
  };

  return (
    <div className="mobile-controls">
      <div className="mobile-controls__up-container">
        <MenuButton
          onClick={() => {
            trigger('w');
          }}
        >
          &uarr;
        </MenuButton>
      </div>
      <div className="mobile-controls__left-right-container">
        <MenuButton
          onClick={() => {
            trigger('a');
          }}
        >
          &larr;
        </MenuButton>
        <MenuButton
          onClick={() => {
            trigger(' ');
          }}
        >
          P
        </MenuButton>
        <MenuButton
          onClick={() => {
            trigger('d');
          }}
        >
          &rarr;
        </MenuButton>
      </div>
      <div className="mobile-controls__down-container">
        <MenuButton
          onClick={() => {
            trigger('s');
          }}
        >
          &darr;
        </MenuButton>
      </div>
    </div>
  );
}

export default MobileControls;
