import { GameWindow } from './utils';

export interface ITimerService {
  start: (callback: () => void, delay: number) => number;
  pause: () => void;
  resume: () => void;
  cancel: () => void;
  destroy: () => void;
}

interface TimerServiceConfig {
  window: GameWindow;
}

export default class TimerService implements ITimerService {
  private window: GameWindow;

  private timerId: number | null = null;

  private startTime = new Date();

  private remaining = 0;

  private callback = () => {};

  constructor({ window }: TimerServiceConfig) {
    this.window = window;
    this.onCallback = this.onCallback.bind(this);
  }

  public start(callback: () => void, delay: number): number {
    if (this.timerId) return this.timerId;
    this.callback = callback;
    this.remaining = delay;
    this.startTime = new Date();
    this.timerId = this.window.setTimeout(this.onCallback, this.remaining);
    return this.timerId;
  }

  public pause() {
    if (this.timerId) {
      this.window.clearTimeout(this.timerId);
      this.remaining -= new Date().getTime() - this.startTime.getTime();
    }
  }

  public resume() {
    if (this.timerId) {
      this.startTime = new Date();
      this.window.clearTimeout(this.timerId);
      this.timerId = this.window.setTimeout(this.onCallback, this.remaining);
    }
  }

  public cancel() {
    if (this.timerId) {
      this.window.clearTimeout(this.timerId);
    }
  }

  public destroy() {
    this.cancel();
    this.callback = () => {};
  }

  private onCallback() {
    this.timerId = null;
    this.callback();
  }
}
