import Cell from '../Cell';

export function randomBetween(min: number = 0, max: number = 1) {
  return Math.floor(Math.random() * max) + min;
}

export type Direction = 'up' | 'down' | 'left' | 'right';

export interface GameSnapshot {
  cells: Cell[];
  score: number;
}

export interface GameWindow {
  setTimeout(handler: TimerHandler, timeout?: number | undefined): number;
  clearTimeout(handle?: number | undefined): void;
  setInterval(handler: TimerHandler, timeout?: number | undefined): number;
  clearInterval(handle?: number | undefined): void;
  addEventListener(
    type: 'keydown',
    listener: (this: Window, ev: KeyboardEvent) => void
  ): void;
  removeEventListener(
    type: 'keydown',
    listener: (this: Window, ev: KeyboardEvent) => void
  ): void;
  dispatchEvent: (event: Event) => boolean;
}
